import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import { VerticalNavComponent } from './component/navigation/vertical-nav/vertical-nav.component';
import { HorizontalNavComponent } from './component/navigation/horizontal-nav/horizontal-nav.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import {CommonModule} from "@angular/common";
import {AppRoutingModule} from "./app-routing.module";
import {PerfectScrollbarModule} from "ngx-perfect-scrollbar";
import {HotelService} from "./services/hotel-service/hotel.service";
import {HttpClient, HttpClientModule} from "@angular/common/http";
import {PlaceService} from "./services/place-service/place.service";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import { MessageService } from './services/message.service';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {RoomService} from "./services/room-service/room.service";


@NgModule({
  declarations: [
    AppComponent,
    VerticalNavComponent,
    HorizontalNavComponent,
    PageNotFoundComponent
  ],
  imports: [
    BrowserModule,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    PerfectScrollbarModule,
  ],
  providers: [
    HotelService,
    PlaceService,
    MessageService,
    RoomService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



