import {Observable} from "rxjs/Observable";
import {Room} from "../../model/room";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {catchError, tap} from "rxjs/operators";
import {of} from "rxjs/observable/of";
import {MessageService} from "../message.service";
import {Injectable} from "@angular/core";

@Injectable()
export class RoomService {
  private baseUrl = 'http://homesty.herokuapp.com/api/v1/room';
  private log(message: string) {
    this.messageService.add('RoomService: ' + message);
  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.log(`${operation} failed: ${error.message}`);
      return of(result as T);
    };
  }
  constructor(
    private http: HttpClient,
    private messageService: MessageService
  ) { }

  public readRoom(): Observable<Room[]> {
    const requestUrl = this.baseUrl;
    return this.http.get<Room[]>(requestUrl)
      .pipe(
        tap(rooms => this.log(`fetched rooms`)),
        catchError(this.handleError('readRooms', []))
      );


  }

  // public addHotel(hotel:  Hotel): Observable<any> {
  //   const requestUrl = this.baseUrl;
  //   return this.http.post<Hotel>(requestUrl, hotel , httpOptions)
  //     .pipe(
  //       tap((hotel: Hotel) => this.log(`added hotel w/ id=${hotel.id}`)),
  //       catchError(this.handleError<Hotel>('addHotel'))
  //     );
  // }
  public readHotelById(id): Observable<any> {
    return this.http.get(this.baseUrl + '/' + id)
      .pipe(
        tap(_ => this.log(`fetched hotel id=${id}`)),
        catchError(this.handleError<Room>(`readHotelById id=${id}`))
      );
  }
  public deleteHotel(id): Observable<any> {
    return this.http.delete(this.baseUrl + '/' + id);
  }
  // public updateHotelById(hotelId: string , hotelUpdate: Room): Observable<any> {
  //   return this.http.put(this.baseUrl + '/' + hotelId , hotelUpdate)
  //     .pipe(
  //       tap((Room: Room) => this.log(`update hotel w/ id=${room.}`)),
  //       catchError(this.handleError<Room>('updateHotelById'))
  //     );
  // }

}



