import { Injectable } from '@angular/core';
import {Observable} from "rxjs/Observable";
import {Hotel} from "../../model/hotel";
import {HttpClient, HttpHeaders} from "@angular/common/http";
import {catchError, tap} from "rxjs/operators";
import {of} from "rxjs/observable/of";
import {MessageService} from "../message.service";

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable()
export class HotelService {
  private baseUrl = 'http://homesty.herokuapp.com/api/v1/hotels';
  private log(message: string) {
    this.messageService.add('HotelService: ' + message);
  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.log(`${operation} failed: ${error.message}`);
      return of(result as T);
    };
    }
  constructor(
    private http: HttpClient,
    private messageService: MessageService
  ) { }

    public readHotels(): Observable<Hotel[]> {
        const requestUrl = this.baseUrl;
        return this.http.get<Hotel[]>(requestUrl)
          .pipe(
            tap(hotels => this.log(`fetched hotels`)),
            catchError(this.handleError('readHotels', []))
          );


    }

    public addHotel(hotel:  Hotel): Observable<any> {
      const requestUrl = this.baseUrl;
      return this.http.post<Hotel>(requestUrl, hotel , {headers: {'Content-Type': 'application/json'}, withCredentials: false})
        .pipe(
          tap(result => this.log(`added hotel w/ id=${result.id}`)),
          catchError(this.handleError<Hotel>('addHotel'))
        );
    }
    public readHotelById(id): Observable<any> {
    return this.http.get(this.baseUrl + '/' + id)
      .pipe(
        tap(_ => this.log(`fetched hotel id=${id}`)),
        catchError(this.handleError<Hotel>(`readHotelById id=${id}`))
      );
    }
    public deleteHotel(id): Observable<any> {
    return this.http.delete(this.baseUrl + '/' + id);
    }
    public updateHotelById(hotelId: string , hotelUpdate: Hotel): Observable<any> {
        return this.http.put(this.baseUrl + '/' + hotelId , hotelUpdate)
          .pipe(
            tap((hotel: Hotel) => this.log(`update hotel w/ id=${hotel.id}`)),
            catchError(this.handleError<Hotel>('updateHotelById'))
          );
    }

}



